.footer__row {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-gap: 0;
  grid-auto-rows: minmax(226px, auto);

  @media (max-width: $screen-sm) {
    grid-auto-rows: minmax(82px, auto);
  }
}

.footer__col--2rows {
  grid-row-end: span 2;
}

.footer__col--big {
  height: 552px;
}

.footer__col--medium {
  height: 406px;

  @media (max-width: $screen-sm) {
    height: 264px;
  }
}

.footer-col1 {
  padding-top: 78px;
  background-color: $color-main;

  @media (max-width: $screen-sm) {
    display: none;
  }
}

.footer-col1__title {
  max-width: 500px;
  margin-bottom: 51px;
  margin-left: auto;
  margin-right: auto;
  font-size: 72px;
  line-height: 86px;
  text-align: center;
  color: $color-secondary;
}

.footer-col1__notice {
  max-width: 343px;
  margin: 0 auto;
  font-size: 36px;
  line-height: 36px;
  text-align: center;
  color: $color-secondary;
}

.footer-col2 {
  overflow: hidden;

  @media (max-width: $screen-sm) {
    display: none;
  }
}

.footer-col2__image {
  width: auto;
  height: 100%;
}

.footer-col3 {
  background-color: $color-green;
}

.footer-socials-nav {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
}

.footer-socials-menu__link {
  font-size: 50px;
  line-height: 115px;
  color: $color-main;
  text-decoration: none;

  @media (max-width: $screen-sm) {
    font-size: 18px;
    line-height: 40px;
  }
}

.footer-col4 {
  background-color: $color-gray-light;
}

.footer-col5 {
  background-color: $color-orange;
}

.footer-col4__link,
.footer-col5__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-size: 50px;
  line-height: 68px;
  color: $color-main;
  text-decoration: none;

  @media (max-width: $screen-sm) {
    font-size: 18px;
    line-height: 24px;
  }
}

.footer-col6 {
  padding: 0 50px;
  background-color: $color-purple;

  @media (max-width: $screen-sm) {
    padding: 0 32px;
  }
}

.footer-col6__image {
  display: block;
  margin: 127px auto 82px;
  width: 90%;
  max-width: 271px;
  height: auto;

  @media (max-width: $screen-sm) {
    max-width: 94px;
    margin-top: 100px;
    margin-bottom: 56px;
  }
}

.footer-col6__notice {
  font-size: 18px;
  line-height: 24px;

  @media (max-width: $screen-sm) {
    font-size: 12px;
    line-height: 16px;
  }
}

.footer-col7 {
  display: flex;
  padding-bottom: 40px;
  background-color: $color-green;

  @media (max-width: $screen-sm) {
    padding-bottom: 28px;
  }
}

.footer-nav {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: auto;

  @media (max-width: $screen-sm) {
    flex-direction: column;
    align-items: center;
    width: 100%;
    text-align: center;
  }
}

.footer-menu {
  margin-left: 75px;

  @media (max-width: $screen-md) {
    margin-bottom: 12px;
  }

  @media (max-width: $screen-sm) {
    margin-top: 20px;
    margin-bottom: 0;
    margin-left: 0;
  }
}

.footer-menu__item {
  margin-bottom: 6px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (max-width: $screen-sm) {
    margin-bottom: 0;
  }
}

.footer-menu--socials {
  .footer-menu__link {
    & > .icon {
      display: none;
    }
  }

  @media (max-width: $screen-sm) {
    display: flex;
    align-items: center;

    .footer-menu__category {
      display: none;
    }

    .footer-menu__item {
      padding-left: 8px;
      padding-right: 8px;
    }

    .footer-menu__link {
      width: 32px;
      height: 32px;

      & > .icon {
        display: block;
        width: 100%;
        height: 100%;
      }

      & > .text {
        display: none;
      }
    }
  }
}

.footer-menu__link {
  font-size: 18px;
  line-height: 24px;
  color: $color-main;
  text-decoration: none;

  @media (max-width: $screen-sm) {
    font-size: 12px;
    line-height: 19px;
  }
}

.footer-menu__category {
  margin-bottom: 8px;
  font-size: 18px;
  line-height: 24px;

  @media (max-width: $screen-sm) {
    font-size: 12px;
    line-height: 16px;
  }
}
